        .about__article__title {
          font-size: 2.369rem; } }
    .about__article__title span {
      display: block;
      padding-top: 4px;
      font-weight: 400; }
      .about__article__title span {
        font-size: 1.266rem; }
        @media screen and (min-width: 20rem) {
