      letter-spacing: 0.1px;
      font-size: 1.2rem;
      color: #444; }
  .about__img {
    height: 320px;
    width: 320px;
    padding: 40px;
    border-radius: 50%; }
  .about__links {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 1px 0 0 0;
    margin: -1px auto 0 auto; }
    .about__links > a {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 128px;
      width: 128px;
      background-color: #1292B4;
      color: #fff;
      border-radius: 50%;
      padding: 0.5em;
      margin-right: 0.8em;
      text-transform: uppercase;
      cursor: pointer; }
      .about__links > a:hover {
        color: #444;
        opacity: 0.9; }

