@media (max-width: 599px) {
  .about {
    flex-direction: column; }
    .about {
      font-size: 0.889rem; } }
    @media screen and (max-width: 599px) and (min-width: 20rem) {
      .about {
        font-size: calc(-0.21262vw + 0.93152rem); } }
    @media screen and (max-width: 599px) and (min-width: 85.375rem) {
      .about {
        font-size: 0.75rem; } }

@media (max-width: 599px) {
    .about__article {
      width: 90%; }
      .about__article__title {
        font-size: 1.602rem; } }
      @media screen and (max-width: 599px) and (min-width: 20rem) {
        .about__article__title {
          font-size: calc(2.37859vw + 1.12628rem); } }
      @media screen and (max-width: 599px) and (min-width: 85.375rem) {
        .about__article__title {
          font-size: 3.157rem; } }

@media (max-width: 599px) {
    .about__links > a {
      height: 96px;
      width: 96px; }
    .about__img {
      height: 192px;
      width: 192px; } }

